body,html{
  line-height: 1;
  font-weight: 200px;
  font-family: 'PingFang SC','STHeitiSC-Light','Helvetica-Light',arial,sans-serif;
}

.clearfix{
  display: inline-block;
  &:after{
    display: block;
    content:".";
    height:0;
    line-height:0;
    clear:both;
    visibility: hidden;
  }
}



