
.public-room {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 44px;
  bottom: 0;
  width: 100%;
  overflow: hidden;
}
.public-room .user-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 80px;
          flex: 0 0 80px;
  background: #f3f5f7;
}
.public-room .user-wrapper .user-item {
  display: table;
  position: relative;
  padding: 0 12px;
  line-height: 54px;
  width: 56px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.public-room .user-wrapper .user-item .name {
  display: table-cell;
  vertical-align: middle;
  width: 56px;
  text-align: center;
  font-size: 20px;
}
.public-room .user-wrapper .user-item .name:last-child {
  border: none;
}
.public-room .user-wrapper .user-item .count {
  display: inline-block;
  padding: 2px;
  line-height: 12px;
  font-size: 12px;
  background: rgba(0, 160, 220, 0.2);
  border-radius: 50%;
}
.public-room .room-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.public-room .room-wrapper .information {
  position: absolute;
  top: 0;
  left: 80px;
  right: 0;
  background: #fff;
  z-index: 10;
  text-align: center;
}
.public-room .room-wrapper .information.slide-enter,
.public-room .room-wrapper .information.slide-leave-active {
  opacity: 0;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.public-room .room-wrapper .information.slide-enter-active,
.public-room .room-wrapper .information.slide-leave-active {
  transition: all .3s linear;
}
.public-room .room-wrapper .information .info-item {
  padding: 10px 0;
  margin: 0 10px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  text-align: left;
}
.public-room .room-wrapper .information .info-item:last-child {
  border: none;
}
.public-room .room-wrapper .information .info-item .info-type {
  display: inline-block;
  width: 65px;
  text-align: center;
  color: rgba(0, 160, 220, 0.5);
  font-size: 14px;
}
.public-room .room-wrapper .information .info-item .info-value {
  color: #000;
  font-size: 16px;
}



