
.chat-box {
  padding: 5px;
}
.chat-box .title {
  line-height: 18px;
  text-align: center;
  font-size: 14px;
  color: #07111b;
}
.chat-box .notice-wrapper {
  margin: 3px 0 7px 0;
  width: 100%;
  text-align: center;
}
.chat-box .notice-wrapper .notice {
  display: inline;
  padding: 0 3px;
  line-height: 12px;
  border-radius: 3px;
  font-size: 10px;
  background: #f3f5f7;
}
.chat-box .notice-wrapper .notice.fade-enter {
  opacity: 0;
}
.chat-box .notice-wrapper .notice.fade-enter-active {
  transition: all .5s ease;
}
.chat-box .message-box {
  position: absolute;
  top: 25px;
  right: 0;
  bottom: 40px;
  padding: 0 0 0 5px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  overflow: hidden;
  z-index: 1;
}
.chat-box .message-box.private {
  left: 0;
}
.chat-box .message-box.public {
  left: 80px;
}
.chat-box .message-box .message-list {
  margin-bottom: 10px;
  padding: 0 5px;
  line-height: 20px;
}
.chat-box .message-box .message-list.current {
  text-align: right;
}
.chat-box .message-box .message-list .text {
  display: inline-block;
  vertical-align: top;
  padding: 5px 10px;
  max-width: 200px;
  word-wrap: break-word;
  word-break: normal;
  text-align: left;
  background: #f3f5f7;
  border-radius: 20px;
  font-size: 16px;
}
.chat-box .message-input {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  bottom: 0;
  right: 0;
  height: 40px;
  box-sizing: border-box;
  padding: 8px 0 5px 0;
  background: #f3f5f7;
  z-index: 41;
}
.chat-box .message-input.private {
  left: 0;
}
.chat-box .message-input.public {
  left: 80px;
}
.chat-box .message-input .message-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin: 0 15px;
}
.chat-box .message-input .message-wrapper .message {
  padding: 3px;
  height: 21px;
  line-height: 21px;
  font-size: 12px;
  width: 100%;
  border-radius: 10px;
}
.chat-box .message-input .emoji-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 35px;
          flex: 0 0 35px;
}
.chat-box .message-input .emoji-wrapper .emoji {
  width: 27px;
  height: 27px;
}
.chat-box .message-input .send-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 35px;
          flex: 0 0 35px;
}
.chat-box .message-input .send-wrapper .send {
  display: inline-block;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 15px;
  padding: 2px 10px;
  line-height: 21px;
  background: #fff;
}
.chat-box .emoji-box {
  position: fixed;
  bottom: 42px;
  left: 80px;
  right: 0;
  padding: 0 0 0 3px;
  background: #f3f5f7;
  z-index: 40;
}
.chat-box .emoji-box.slide-enter,
.chat-box .emoji-box.slide-leave-active {
  opacity: 0;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
.chat-box .emoji-box.slide-enter-active,
.chat-box .emoji-box.slide-leave-active {
  transition: all .5s linear;
}
.chat-box .emoji-box .emoji-item {
  float: left;
  width: 20px;
  height: 20px;
  padding: 4px;
}



