body,
html {
  line-height: 1;
  font-weight: 200px;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@font-face {
  font-family: 'sell-icon';
  src: url('../fonts/sell-icon.eot?jgkyc3');
  src: url('../fonts/sell-icon.eot?jgkyc3#iefix') format('embedded-opentype'), url('../fonts/sell-icon.ttf?jgkyc3') format('truetype'), url('../fonts/sell-icon.woff?jgkyc3') format('woff'), url('../fonts/sell-icon.svg?jgkyc3#sell-icon') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'sell-icon' !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-chat:before {
  content: "\e900";
  color: #1296db;
}
.icon-back:before {
  content: "\e901";
}
.icon-close:before {
  content: "\e903";
}



