
.information {
  position: relative;
}
.information .user-wrapper {
  position: absolute;
  left: 10px;
  top: -5px;
}
.information .user-wrapper img {
  border-radius: 50%;
}
.information .user-wrapper .username {
  display: inline-block;
  vertical-align: top;
  line-height: 32px;
  font-size: 18px;
}
.information .user-information {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 100px;
  z-index: 50;
  overflow: auto;
  max-width: 375px;
}
.information .user-information.fade-enter,
.information .user-information.fade-leave-active {
  opacity: 0;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.information .user-information.fade-enter-active,
.information .user-information.fade-leave-active {
  transition: all 0.5s ease-in;
}
.information .user-information .info-avatar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  height: 200px;
  z-index: 100;
}
.information .user-information .info-avatar .blur {
  position: absolute;
  left: 0;
  width: 100%;
  height: 200px;
  background: rgba(7, 17, 27, 0.9);
  -webkit-filter: blur(10px);
          filter: blur(10px);
  z-index: 100;
}
.information .user-information .info-avatar img {
  display: inline-block;
  z-index: 100;
  border-radius: 50%;
  width: 70%;
  max-width: 140px;
  height: 70%;
}
.information .user-information .info-avatar .change-avatar {
  position: absolute;
  top: 0;
  bottom: 0;
  right: -64px;
  width: 64px;
  overflow: hidden;
  z-index: 200;
}
.information .user-information .info-avatar .change-avatar .avatar-item {
  box-sizing: border-box;
  padding: 10px;
  width: 64px;
  height: 64px;
}
.information .user-information .info-details {
  position: absolute;
  left: 0;
  right: 0;
  top: 200px;
  bottom: 30px;
  padding-bottom: 20px;
  overflow: hidden;
  z-index: 100;
  margin-top: 20px;
  margin-bottom: 10px;
}
.information .user-information .info-details .info-item {
  position: relative;
  padding: 10px 0;
  margin: 0 10px;
  text-align: left;
  border-bottom: 1px solid rgba(147, 153, 159, 0.1);
  background: #fff;
  z-index: 100;
}
.information .user-information .info-details .info-item:last-child {
  border: none;
}
.information .user-information .info-details .info-item .info-type {
  display: inline-block;
  width: 60px;
  text-align: center;
  color: rgba(0, 160, 220, 0.5);
  font-size: 14px;
}
.information .user-information .info-details .info-item .info-value,
.information .user-information .info-details .info-item .info-input {
  color: #000;
  font-size: 16px;
}
.information .user-information .info-details .info-item .info-input {
  padding: 2px 5px;
  border: 1px solid #fff;
  border-radius: 5px;
  background: rgba(147, 153, 159, 0.2);
}
.information .user-information .info-details .info-item .edit {
  position: absolute;
  top: 13px;
  right: 5px;
  color: rgba(147, 153, 159, 0.5);
  font-size: 16px;
}
.information .user-information .info-details .edit-selection {
  margin-top: 10px;
  font-size: 0;
}
.information .user-information .info-details .edit-selection .save,
.information .user-information .info-details .edit-selection .cancel {
  margin: 0 5px;
  padding: 5px;
  border-radius: 10px;
  font-size: 14px;
  color: #fff;
  background: #00a0dc;
}
.information .user-information .advanced-operation {
  position: fixed;
  left: 0;
  bottom: 0;
  right: 100px;
  padding: 0 0 5px 0;
  max-width: 375px;
  font-size: 16px;
  z-index: 100;
  background: #fff;
}
.information .user-information .advanced-operation .base-information {
  margin: 10px 0;
  text-align: left;
}
.information .user-information .advanced-operation .base-information label {
  display: inline-block;
  width: 30%;
  text-align: center;
  color: rgba(147, 153, 159, 0.5);
  font-size: 16px;
}
.information .user-information .advanced-operation .base-information input {
  display: inline-block;
  width: 65%;
  background: rgba(147, 153, 159, 0.2);
  border-radius: 3px;
}
.information .user-information .advanced-operation .change-base {
  display: block;
  padding: 2px 5px;
  border-radius: 5px;
  text-align: center;
  background: #00a0dc;
  color: white;
}
.information .user-information .cancel-layer {
  position: fixed;
  bottom: 0;
  top: 0;
  left: 0;
  right: 100px;
  max-width: 375px;
  z-index: 75;
  background: #fff;
}



