
.login {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.login .logo-wrapper {
  line-height: 50px;
  width: 100%;
  height: 110px;
  text-align: center;
}
.login .logo-wrapper .title {
  font-size: 50px;
  font-weight: 700;
  font-style: italic;
  color: #00a0dc;
}
.login .logo-wrapper .icon-chat {
  font-size: 50px;
}
.login .login-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  box-sizing: border-box;
  padding: 10px;
  line-height: 16px;
  width: 100%;
  height: 50%;
  font-size: 0px;
}
.login .login-wrapper .name,
.login .login-wrapper .password,
.login .login-wrapper .confirm-password {
  margin-bottom: 25px;
  font-size: 16px;
}
.login .login-wrapper .name label,
.login .login-wrapper .password label,
.login .login-wrapper .confirm-password label {
  text-align: left;
  padding: 3px;
  display: inline-block;
  width: 80px;
}
.login .login-wrapper .name input,
.login .login-wrapper .password input,
.login .login-wrapper .confirm-password input {
  padding: 3px;
  border: 1px solid rgba(7, 17, 27, 0.1);
  border-radius: 10px;
  font-size: 16px;
}
.login .login-wrapper .move-enter,
.login .login-wrapper .move-leave-active {
  opacity: 0;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.login .login-wrapper .move-enter-active,
.login .login-wrapper .move-leave-active {
  transition: all 1s linear;
}
.login .login-wrapper .login-select {
  text-align: center;
}
.login .login-wrapper .login-select .confirm {
  display: block;
  padding: 2px 4px;
  background: #00a0dc;
  border-radius: 10px;
  line-height: 16px;
  margin: 0 0 10px 33.5%;
  width: 60%;
  font-size: 16px;
  color: #fff;
}
.login .login-wrapper .login-select .regist {
  margin: 0 0 10px 33.5%;
  cursor: pointer;
  font-size: 10px;
  color: #93999f;
}
.login .login-wrapper .message {
  padding: 10px;
  text-align: center;
}
.login .login-wrapper .message .text {
  margin-left: 33.5%;
  line-height: 14px;
  font-size: 12px;
  color: #f01414;
}
.login .login-wrapper .message .text.fade-enter {
  opacity: 0;
}
.login .login-wrapper .message .text.fade-enter-active {
  transition: all 0.5s ease-in;
}



