
.header {
  position: relative;
  padding: 10px 0;
  line-height: 24px;
  width: 100%;
  text-align: center;
  background: #f3f5f7;
  font-size: 24px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}
.header .icon-chat {
  font-size: 20px;
}
.header .icon-close {
  position: absolute;
  right: 10px;
  top: 10px;
}



